/**
 * Centralized test utilities - Main entry point
 * 
 * This module provides a comprehensive set of testing utilities including:
 * - Test factories for creating consistent test data
 * - Standardized mock patterns
 * - Shared setup/teardown utilities
 * - Custom render functions with providers
 * - Performance testing utilities
 */

// Export factories
export * from './factories';

// Export mocks
export * from './mocks';

// Export setup utilities
export * from './setup/shared-setup';

// Export render utilities
export * from './render';

// Re-export existing utilities for backward compatibility
export * from './test-helpers';
export * from './upload-test-helpers';

// Main factory instances for convenience
export { testFactories } from './factories';
export { standardMocks } from './mocks';

/**
 * Quick setup function for most common test scenarios
 */
export const setupTest = (options: {
  includeAPI?: boolean;
  includeFileSystem?: boolean;
  includeNavigation?: boolean;
  timeout?: number;
} = {}) => {
  const { setupFullTestEnvironment } = require('./setup/shared-setup');
  return setupFullTestEnvironment(options);
};

/**
 * Quick mock setup for common scenarios
 */
export const setupMocks = () => {
  const { standardMocks } = require('./mocks');
  standardMocks.setupAll();
  return standardMocks;
};

/**
 * Quick factory access
 */
export const createTestData = () => {
  const { testFactories } = require('./factories');
  return testFactories;
};
